.home {
  background-color: white;
  display: flex;
  flex-direction: column;
}

.home .filters {
  display: flex;
  list-style: none;
  justify-content: flex-start;
  padding: 20px;
}

.home .filters li {
  margin-right: 20px;
  background-color: #e1e4ea;
  border-radius: 10px;
  padding: 3px 10px;
}

.home .emails-layout{
    display: grid;
    grid-template-columns: 100%
}

.home .emails-layout-clicked{
    display: grid;
    grid-template-columns: 40% 60%;
}
.home .emails {
  display: flex;
  flex-direction: column;
  padding: 10px;
}

.home .emails .email-card {
  height: auto;
  background-color: #e1e4ea;
  margin-bottom: 20px;
  display: flex;
  padding: 5px 20px;
  border-radius: 20px;
}

.home .emails .email-card .avatar ,.home .full-email-body .avatar {
  width: 50px;
  height: 50px;
  background-color: #e54065;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 20px;
  font-weight: bold;
  color: white;
}

.home .emails .email-card .email-body {
  margin-left: 30px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  font-size: 13px;
}

.home .full-email-body{
    padding: 20px;
    display: grid;
    grid-template-columns: 10% 90%;
}

.title-layout{
    display: flex;
    justify-content: space-between;
}

.title-layout .title{
    font-size: 20px;
    font-weight: bold;
}
.markasread{
    display: flex;
    flex-direction: column;
}
.markasread button{
    padding: 5px 20px;
    background-color: #e54065;
    color: white;
    border-radius: 10px;
    outline: none;
    border: none;
    margin-bottom: 10px;
    cursor: pointer;
}